import { Component } from "react";
import { Modal, Table, Row, Col } from "react-bootstrap";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import BreakDownChart from "../Conversion/BreakDownChart";
import { kFormatter } from "../../../../../utils";

class BreakDownModal extends Component {
    state = { colors: ['#008ffb', '#00e396', '#fe8900', '#2dbb74'] }
    render() {
        const { isPopupOn, onCloseModel, breakDownItem, breakDownImage, userData } = this.props
        const cartData = [parseFloat(breakDownItem?.viewCount), parseFloat(breakDownItem?.ctaClick)]
        const { colors } = this.state
        const clicksLabel = ['Post views', 'CTA clicks']
        return <Modal size="lg" show={isPopupOn} onHide={onCloseModel} centered>
            <Modal.Header className="" closeButton>
                <Modal.Title>Breakdown</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className='overflow-auto' style={{height:500}}>
                    <div className="p-3">
                        <Row>
                            <Col sm={5}>
                                <ImageType mediaClass={'position-relative overflow-hidden w-100 mb-5'} size={100} height={200} width={200} url={breakDownImage} isCover={true} alt={""} />
                            </Col>
                            <Col sm={7}>
                                <div className="mb-5">
                                    <BreakDownChart colors={colors} className='donut-chart' cartLabel={['Post views', 'CTA clicks']} breakDownItem={breakDownItem} key={Math.random() * 6} website={true}/>
                                </div>
                                <div className="shopClickSection">
                                    <h5>Shop Clicks</h5>
                                    <Row>
                                    {clicksLabel.map((Item, index) => {
                                    return <Col xs={6} key={index}>
                                            <div className={`d-flex align-items-center justify-content-start`}>
                                                <div className="h-12px w-12px rounded-circle me-2" style={{backgroundColor:colors[index]}}></div>
                                                <span className="fw-medium text-muted">{Item}</span>
                                            </div>
                                        </Col>
                                    })}

                                    </Row>
                                </div>
                            </Col>
                        </Row>
                    </div>
                    {breakDownItem && breakDownItem.products && breakDownItem.products.length ? <div className="viewBreakdown_table">
                        <Table size='lg' responsive>
                            <thead>
                                <tr>
                                    <th className="bg-light border-0">Products</th>
                                    {userData?.sessionData?.createFrom && userData?.sessionData?.createFrom.toLowerCase() == 'shopify' ?
                                    <th className="bg-light border-0">Add to Cart</th> 
                                    :
                                    <th className="bg-light border-0">CTA clicks </th>}
                                    <th className="bg-light border-0">Assisted</th>
                                </tr>
                            </thead>
                            <tbody>
                                {breakDownItem?.products?.map((post, index) => {
                                    return <tr key={index}>
                                        <td>
                                            <div className="d-flex align-items-center">
                                                <div className="w-50px me-3">
                                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={100} width={100} url={post.product_image_new} isCover={true} alt={post.product_title} isProduct={true}/>
                                                </div>
                                                <div className="overflow-hidden">
                                                    <span className="d-block fs-8 fw-medium text-ellipsis" style={{ maxWidth: 200 }}>{post.product_title}</span>
                                                </div>
                                            </div>
                                        </td>
                                        <td>{post.ctaClick}</td>
                                        <td>{post.currency}{kFormatter(post.assisted)} </td>
                                    </tr>
                                })}

                            </tbody>
                        </Table>
                    </div> : <></>}
                </div>
            </Modal.Body>
        </Modal>
    }
}

export default BreakDownModal