import React, { Component } from 'react';
import ReactApexChart from "react-apexcharts";
import ApexCharts from 'apexcharts';
import { reArrangeConversion, reArrangeSeries, reArrangeShopOnChart } from '../../../../utils';
import _ from 'lodash';

class AreaChart extends Component {

    state = {
        series: [],
        options: {
            chart: {
                height: 350,
                id: 'area-datetime',
                zoom: { enabled: false },
                type: 'area',
                animations: {
                    enabled: true,
                    easing: 'linear',
                    dynamicAnimation: {
                        speed: 1000
                    }
                }
            },
            colors:['#2f84ed', '#fe8900', '#fec600', '#2dbb74', '#f83434'],
            legend: {
                show: true,
                position: 'top',
                itemMargin: {
                    horizontal: 20,
                    vertical: 0
                },
            },
            grid: {
                show: true,
                borderColor: 'var(--bs-gray-100)',
            },
            stroke: { curve: 'smooth', width: [2, 2, 2] },
            dataLabels: { enabled: false },
            markers: { size: 0, style: 'hollow' },
            xaxis: { type: 'datetime', min: 0, tickAmount: 6, },
            tooltip: {
                x: { format: 'dd MMM yyyy' }
            },
        },
    };

    componentDidMount() {
        const { chartData, convert, shopOn } = this.props
        if (chartData && Object.keys(chartData).length > 0) {
            if (convert) this.setConversion(chartData)
            else if (shopOn) this.setShopOn(chartData)
            else this.setSeries(chartData)
        }
    }

    componentDidUpdate(prevProps) {
        const { chartData, convert, shopOn } = this.props
        if (!_.isEqual(prevProps.chartData, chartData)) {
            if(!(chartData && Object.keys(chartData).length > 0)) return;
            if (convert) this.setConversion(chartData)
            else if (shopOn) this.setShopOn(chartData)
            else this.setSeries(chartData)
        }
    }


    setSeries = (chartData) => {
        if(chartData){
            const data = reArrangeSeries(chartData)
            const lastDate = data?.unixDate[(data?.unixDate?.length) - 1]
        this.setState(prevState => ({
            series: data.newSeries,
            options: {
                ...prevState.options,
                xaxis: {
                    ...prevState.options.xaxis,
                    min: data?.unixDate[0]
                }
            }
        }))

        if (lastDate) {
            const chart = ApexCharts.getChartByID('area-datetime2');
            if (chart) { chart.zoomX(data?.unixDate[0], data?.unixDate[lastDate - 1]); }
        }
    }
    }

    setConversion = (chartData) => {
        if(chartData){
        const data = reArrangeConversion(chartData)
        const lastDate = data?.unixDate[(data?.unixDate?.length) - 1]
        this.setState(prevState => ({
            series: data.newSeries,
            options: {
                ...prevState.options,
                xaxis: {
                    ...prevState.options.xaxis,
                    min: data?.unixDate[0]
                },
                y: {
                    formatter: (value, { series, seriesIndex }) => {
                        if (seriesIndex === 1) {
                            return `${this.props?.currency}` + value;
                        }
                        return value;
                    }
                }
            }
        }))

        if (lastDate) {
            const chart = ApexCharts.getChartByID('area-datetime2');
            if (chart) { chart.zoomX(data?.unixDate[0], data?.unixDate[lastDate - 1]); }
        }
    }
    }

    setShopOn = (pageData) => {
        const { serialNum } = this.props
        const data = reArrangeShopOnChart(pageData)
        const lastDate = data?.unixDate[(data?.unixDate?.length) - 1]
        this.setState(prevState => ({
            series: [data.newSeries[serialNum]] || [],
            options: {
                ...prevState.options,
                xaxis: {
                    ...prevState.options.xaxis,
                    min: data?.unixDate[0]
                }
            }
        }))

        if (lastDate) {
            const chart = ApexCharts.getChartByID('area-datetime2');
            if (chart) { chart.zoomX(data?.unixDate[0], data?.unixDate[lastDate - 1]); }
        }
    }

    render() {
        const { options, series } = this.state
        return <div className="chartArea">
            <div id="chart">
                <ReactApexChart options={options} series={series} type="area" height={350} />
            </div>
        </div>
    }
}

export default AreaChart 