import React from 'react'
import { Tab } from 'react-bootstrap';
import DonutChart from '../../../CommonComponents/DonutChart';

const PostType = ({postNetwork, loader, loaderData}) => {
    return (
        <Tab.Pane eventKey="postType" className='p-4'>
            <DonutChart chartData={loader ? loaderData : postNetwork ? postNetwork : null} colors={['#FC5362', '#FEC737', '#5096FF']} hideLegend={true} height={300} />
            {!loader ? <div className="w-100 mt-4">
                <div className="f-between w-100">
                    {postNetwork && postNetwork[0] && postNetwork[0].labels.length > 0 ? postNetwork[0].labels.map((lItem, index) => <div key={Math.random() * (6 + index)} className="d-flex flex-column text-center m-auto">
                        <p className="mb-2 fs-8 fw-medium text-muted">{lItem}</p>
                        <h3 className="text-center h2">{postNetwork[0].data[index]}</h3>
                        <div className="post_score_border"></div>
                    </div>) : null
                    }
                </div>
            </div> : null}
        </Tab.Pane>
    )
}

export default PostType