import React, { useMemo } from 'react'
import { Card, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { kFormatter } from '../../../../../utils';
import { useSelector } from 'react-redux';
import { PostOverviewSkeleton } from './Skeleton';

const PostOverview = () => {
    const { post_overview, post_overview_loader } = useSelector((state) => state.analyticsOverView);
    const content = useMemo(() => window?.APP_CONTENT?.Analytics ?? {}, []);
    const { headings, overview_content } = content;
    return (
        <Card className="card-stretch">
            <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                <h3 className="h5 mb-0">{headings?.post_overview?? "Post Overview"}</h3>
                <OverlayTrigger placement='top' overlay={<Tooltip>{headings?.post_overview_info ?? "Multiply your social UGC by collaborating with your top listed Creators"}</Tooltip>} >
                    <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
                </OverlayTrigger>
            </div>
            <Card.Body className='px-4 py-1 overflow-auto' style={{ height: 300 }}>
                { post_overview_loader ? <PostOverviewSkeleton /> : post_overview.map((item, key) => {
                    const { name, description, type } = overview_content?.find((content) => {
                        const tabName = item.name?.includes("Visitors") ? "Widget Views" : item.name;
                        return content.type == tabName
                    }) || {};
                    return (
                        <div className="px-5 py-5 border rounded my-3" key={key}>
                            <div className="d-flex align-items-center justify-content-between">
                                <div className="d-flex align-items-start me-4">
                                    <div className="post-icon me-3 d-flex align-items-center justify-content-center border">
                                        {
                                            type == "Widget Views" ?
                                                <svg width="20" height="17" viewBox="0 0 20 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M9.67874 0.825439C14.4937 0.825439 18.4995 4.28993 19.3394 8.86211C18.4995 13.4343 14.4937 16.8988 9.67874 16.8988C4.86372 16.8988 0.857908 13.4343 0.0180664 8.86211C0.857908 4.28993 4.86372 0.825439 9.67874 0.825439ZM9.67874 15.1129C13.461 15.1129 16.6974 12.4804 17.5166 8.86211C16.6974 5.24385 13.461 2.61137 9.67874 2.61137C5.89641 2.61137 2.65997 5.24385 1.84073 8.86211C2.65997 12.4804 5.89641 15.1129 9.67874 15.1129ZM9.67874 12.8805C7.45944 12.8805 5.66037 11.0814 5.66037 8.86211C5.66037 6.64285 7.45944 4.84378 9.67874 4.84378C11.8979 4.84378 13.6971 6.64285 13.6971 8.86211C13.6971 11.0814 11.8979 12.8805 9.67874 12.8805ZM9.67874 11.0945C10.9117 11.0945 11.9111 10.095 11.9111 8.86211C11.9111 7.6292 10.9117 6.6297 9.67874 6.6297C8.44582 6.6297 7.44629 7.6292 7.44629 8.86211C7.44629 10.095 8.44582 11.0945 9.67874 11.0945Z" fill="#637079" />
                                                </svg> :
                                                type == "Show More Clicks" ?
                                                    <svg width="23" height="23" viewBox="0 0 23 23" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M14.4471 12.8432L16.7268 19.1066L12.5312 20.6337L10.2516 14.3702L6.77002 16.5538L8.21533 2.24878L18.5175 12.278L14.4471 12.8432ZM14.4377 18.0392L12.0133 11.3781L14.6559 11.0112L9.62033 6.10899L8.91388 13.1011L11.1742 11.6835L13.5986 18.3446L14.4377 18.0392Z" fill="#637079" />
                                                    </svg>
                                                    :
                                                    type == "CTA Click" ?
                                                        <svg width="23" height="22" viewBox="0 0 23 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                            <path d="M2.74805 4.00625C2.74805 3.51308 3.14784 3.11328 3.64101 3.11328H19.7144C20.2075 3.11328 20.6073 3.51308 20.6073 4.00625V18.2937C20.6073 18.7869 20.2075 19.1866 19.7144 19.1866H3.64101C3.14784 19.1866 2.74805 18.7869 2.74805 18.2937V4.00625ZM4.53397 12.9359H18.8214V4.89921H4.53397V12.9359ZM4.53397 14.7218V17.4007H8.10583V14.7218H4.53397ZM9.89176 14.7218V17.4007H13.4636V14.7218H9.89176ZM15.2495 14.7218V17.4007H18.8214V14.7218H15.2495Z" fill="#637079" />
                                                        </svg>
                                                        :
                                                        type == "Post Clicks" ?
                                                            <svg width="24" height="23" viewBox="0 0 24 23" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                                <path d="M15.8772 8.92725L7.78731 17.0171L6.45801 15.6878L14.5479 7.59794H7.41754V5.71802H17.7571V16.0576H15.8772V8.92725Z" fill="#637079" />
                                                            </svg> : null
                                        }

                                    </div>
                                    <div className="">
                                        <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name">
                                            {name || ""}
                                        </p>
                                        <p className="mb-0" style={{ color: '#959595' }}>{description || ""}</p>
                                    </div>
                                </div>
                                <div className="d-flex align-items-center">
                                    <p className="font-weight-boldest text-dark display-6 m-0">{kFormatter(item?.count ?? 0)}</p>
                                    {/* <Percentage type={item?.flag == 0 ? "down" : "up"} value={item?.difference ?? ""} /> */}
                                </div>
                            </div>
                        </div>
                    );
                })}
            </Card.Body>
        </Card>
    )
}

export default PostOverview