import React, { Component } from 'react';
import { Col, OverlayTrigger, Row, Tooltip, Card } from 'react-bootstrap';
import { connect } from 'react-redux';
import { kFormatter } from '../../../../../utils';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';

class TopCounts extends Component {
    render() {
        const { counts, loader } = this.props;
        const overview_content = window?.APP_CONTENT?.Analytics?.overview_content || [];

        const color = ['blue', 'orange', 'yellow', 'green']
        return counts?.map((item, index) => {
            const { name, tooltip } = overview_content?.find((content) => item?.name?.includes(content?.type ?? "")) || {};

            if (!String(item.name).includes('Recommendation')) { // prouser condition
                return <Col lg={3} sm={6} className='col-6' key={index}>
                    <Card className={`${color[index]}`}>
                        <Card.Body>
                            <div className="d-flex flex-column">
                                <div className="d-flex">
                                    <p className="fs-8 fw-medium text-muted mb-2 me-2">{name || ""}</p>
                                    <OverlayTrigger placement='top' overlay={<Tooltip>{tooltip || ""}</Tooltip>} >
                                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
                                    </OverlayTrigger>
                                </div>
                                <span className="h1 mb-0">{item.currency ? item.currency : ''}{loader ? 0 : kFormatter(item.count)}</span>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
            }
        })
    }
}

const mapStateToProps = (state) => {
    return {
        counts: state.analyticsOverView?.counts,
        loader: state.analyticsOverView?.counts_loader,
    }
}

export default connect(mapStateToProps)(TopCounts)