import { Component } from "react"
import { connect } from "react-redux"
import { Card, Row, Col } from "react-bootstrap";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import PopularProductSkel from "../Skeletons/PopularProductSkel";
import { kFormatter } from "../../../../../utils"

class PopularProduct extends Component {
    render() {
        const {products, loader, content }= this.props
        return <Card className="card-stretch gutter-b">
                <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                    <span className="h5 mb-0">{content?.popular_product ?? ""}</span>
                </div>
                <Card.Body className="overflow-auto" style={{height:500}}>

                        {loader ? 
                        <PopularProductSkel />
                        : 
                        products && products.length? products?.map((item ,index)=>{ 
                            return <div key={index} className="border-bottom mb-2 p-2" data-id={item.id}>
                                        <div className="d-flex">
                                            <div className="d-flex align-items-center w-100">
                                                <div className="d-flex flex-shrink-0 flex-column w-70px cursor-pointer pe-2">
                                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={200} width={200} url={item.product_image} alt={''} isCover={true} isProduct={true}/>
                                                </div>
                                                <div className="pe-2 overflow-hidden w-100">
                                                    <p className="w-100 mb-0" style={{maxWidth:200}}><span className="fw-bold line-clamp line-clamp-2">{item.product_title}</span></p>
                                                    <p className="mb-1 text-muted fw-medium">{item.product_sku}</p>
                                                    <p className="mb-1 text-gray-500 text-primary fw-bold">{item.discount_currency_symbol}{item.price}</p>
                                                </div>
                                            </div>
                                            <div className="d-flex flex-grow-1 justify-content-end w-100 overflow-hidden">
                                                <Row style={{maxWidth:450}}>
                                                    <Col xs={6} className="d-flex align-items-center justify-content-between py-1">
                                                        <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                        {content?.product_view ?? ""}:</span>
                                                        <h6 className="fs-8 mb-0 text-end" style={{minWidth:'50px'}}>{kFormatter(item.view)}</h6>
                                                    </Col>
                                                    <Col xs={6} className="d-flex align-items-center justify-content-between py-1">
                                                        <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                        {content?.click_cta ?? ""}:</span>
                                                        <h6 className="fs-8 mb-0 text-end" style={{minWidth:'50px'}}>{kFormatter(item.ctaClick)}</h6>
                                                    </Col>
                                                    <Col xs={6} className="d-flex align-items-center justify-content-between py-1">
                                                        <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                        {content?.cta_through}:</span>
                                                        <h6 className="fs-8 mb-0 text-end" style={{minWidth:'50px'}}>{parseFloat(parseFloat((item?.ctaClick/item?.view)*100).toFixed(2)).toString() }%</h6>
                                                    </Col>
                                                    {/* <Col xs={6} className="d-flex align-items-center justify-content-between py-1">
                                                        <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                        CTA clicks:</span>
                                                        <h6 className="fs-8 mb-0 text-end" style={{minWidth:'50px'}}>{kFormatter(item.ctaClick)}</h6>
                                                    </Col> */}
                                                </Row>
                                            </div>
                                        </div>
                                </div>
                        
                    })  
                    : <NoCardData title={content?.no_data ?? ""} desc="" methodTitle="" method={''} />
                    }
                </Card.Body>
            </Card>
    }
}


const mapStateToProps=(state)=>{
    return { 
        products: state.analyticsShopOn?.shopOntopProduct_data,
        loader: state.analyticsShopOn?.shopOntopProduct_loader,
    }
}

export default connect(mapStateToProps)(PopularProduct)