import _ from "lodash";
import { Component } from "react";
import ReactApexChart from "react-apexcharts";

class DonutChart extends Component{
    state = {
        series: [1, 1],
        options: {
            dataLabels: {
                enabled: false,
            },
            legend: {
                show: false,
                fontSize: '14px',
                position: 'bottom',
                itemMargin: {
                    horizontal: 10,
                },
            },
            colors: this.props.colors,
            chart: {
                type: 'donut',
            },
            stroke:{
                colors:['var(--bs-gray-100)']
            },
            plotOptions: {
                pie: {
                    donut: {
                        stroke: 0,
                        legend: {
                            show: false,
                        },
                        labels: {
                            show: true,
                            total: {
                                label: 'Loading...',
                                showAlways: true,
                                show: true
                            },
                            value: {
                                show: false
                            },
                            style: {
                                colors:['var(--bs-gray-800)']
                            },
                        }
                    }
                }
            },
            chart: {
                animations: {
                    enabled: true,
                    easing: 'easeinout',
                    speed: 800,
                    animateGradually: {
                        enabled: true,
                        delay: 2000
                    },
                    dynamicAnimation: {
                        enabled: true,
                        speed: 350
                    }
                }
            },
            tooltip:{
                enabled:false,
            },
            labels: []
        }
    }

    componentDidMount(){
        const {chartData, convert} = this.props
        if(chartData && Object.keys(chartData).length > 0){
            // if (convert) this.setConversion(chartData)
            // else this.setSeries(chartData)
            this.setSeries(chartData)
        } 
    }

    componentDidUpdate(prevProps){
        const {chartData, convert} = this.props
        if (!_.isEqual(prevProps.chartData, chartData) && chartData && Object.keys(chartData).length > 0){
            //    if(convert) this.setConversion(chartData)
            //    else this.setSeries(chartData)
            this.setSeries(chartData)
        }
    }


    setSeries = (chartData) => {
        const {hideLegend} = this.props
        const seriesData = chartData[0].data
        this.setState(prevState => ({
            series: seriesData ? seriesData : [1],
            options: {
                ...prevState.options,
                labels:  chartData[0].labels,
                colors: !seriesData ? ['var(--bs-gray-200)']:this.props.colors,
                plotOptions: {
                    pie: {
                        donut: {
                            labels: {
                                show: true,
                                total: {
                                    label: seriesData ? 'Total' : 'Loading...',
                                },
                                value: {
                                    show: seriesData ? true : false,
                                }
                            }
                        }
                    }
                },
                tooltip:{
                    enabled:seriesData ? true : false
                },
                legend: {
                    show:seriesData && !hideLegend ? true : false
                },
            }
        }))
    }

    render () {
        const {height} = this.props
        const {options, series} = this.state
        return <div className="channerlChart donut-chart">
            <ReactApexChart options={options} series={series} type="donut" height={height?height:350} />
        </div>
    }
}

export default DonutChart