import React, { useCallback, useEffect, useState } from 'react'
import { Button, Container, Dropdown, Form, Nav, NavDropdown } from 'react-bootstrap';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import moment from 'moment';

const addOrRemove = (list, item) => {
    const map = new Map((list || []).map(i => [i.id, i]));

    if (map.has(item.id)) map.delete(item.id);
    else map.set(item.id, item);

    const updatedList = [...map.values()];
    return updatedList;
}

const AnalyticsHeader = ({ isShopify, activeTab, shoppableStatus, isDisabled, wallList, generatePDFLoader, generatePDF, getIds, addWebsiteListId, addWallId, overViewList, getOverView, websiteList, overViewWall, websiteWall, handleState, filterIds, updateDateFilter, shopOnList }) => {

    const [state, setState] = useState({
        startDate: moment().subtract(30, 'days').format("DD-MM-YYYY"),
        endDate: moment().format("DD-MM-YYYY"),
        maxDate: moment().endOf('month').format("DD-MM-YYYY"),
        selectedShopOn: [],
        selectedWebList: [],
        selectedWallList: [],
        shopOnWall: 0,
    })

    const { startDate, endDate, maxDate, selectedShopOn, selectedWallList, selectedWebList, shopOnWall } = state;

    const handleOnApply = (event, picker) => {
        setState(prevState => ({...prevState, startDate: picker.startDate.format("DD-MM-YYYY"), endDate: picker.endDate.format("DD-MM-YYYY") }))
    }

    const handleDateRange = (start, end) => {
        let wallIds = overViewWall;
        let webIds = state?.selectedWebList?.map(i => i.id);
        if (!overViewWall?.length) {wallIds = [0]; webIds=''}
        else wallIds = filterIds(wallIds, 'overViewWall')
        updateDateFilter({ startDate: start, endDate: end }, wallIds, webIds, shopOnWall)
        setState(prevState => ({ ...prevState, startDate: start.format("DD-MM-YYYY"), endDate: end.format("DD-MM-YYYY") }))
    }

    const handleShopOnToggleSelect = (item) => {
        const updatedList = addOrRemove(selectedShopOn, item);
        const ids = getIds(updatedList).join(',');
        const allSelected = updatedList?.length === shopOnList?.length;
        setState(prevState => ({...prevState, selectedShopOn: updatedList, shopOnWall: allSelected ? 0 : ids }));
    };

    const getShopOnDisplayText = () => {
        if (selectedShopOn?.length === 0) return 'All ShopOn';
        else if (selectedShopOn?.length === 1) return selectedShopOn[0].name;
        return `${selectedShopOn?.length} ShopOn`;
    }

    const handleToggleSelect = (listName, item, callback = () => { }) => {
        setState(prevState => {
            const list = prevState[listName];
            const updatedList = addOrRemove(list, item);
            const ids = getIds(updatedList);
            if (listName == 'selectedWebList') addWebsiteListId(ids);
            else addWallId(ids);
            return { ...prevState, [listName]: updatedList };
        });
        callback();
    };

    const memoHandleWallChange = useCallback((item, status) => {
        item?.forEach(element => {
            const res = websiteList.filter(i => i?.wall?.ThemeInfo?.wallId === element?.id)
            filterWebsiteList('selectedWebList', res, status)
        });

    }, [websiteList]);

    const memoChangeWall = useCallback((item, type) => {
        const newId = type ? 0 : item.id;
        const targetKey = activeTab === 5 ? "shopOnWall" : "overViewWall";
        handleState(targetKey, newId);
    }, [activeTab, shopOnWall, overViewWall]);


    const filterWebsiteList = (listName, data, status) => {
        setState(prevState => {
            let updatedList = [...(prevState[listName] || [])];
            data?.forEach(item => {
                const webId = item?.wall?.ThemeInfo?.webId;
                const wallName = item?.wall?.ThemeInfo?.wallName;
                const wallId = item?.wall?.ThemeInfo?.wallId;
                if (status) updatedList = [...updatedList, { name: wallName, id: webId, wallId }];
                else updatedList = updatedList.filter(i => i.id !== webId);
            });
            addWebsiteListId(getIds(updatedList))
            return {
                ...prevState, [listName]: updatedList
            };
        });
    }

    const getDisplayText = (status) => {
        if (state[status ? 'selectedWebList' : 'selectedWallList']?.length === 0) return status ? 'All Website' : 'All Gallery';
        if (state[status ? 'selectedWebList' : 'selectedWallList']?.length === 1) return state[status ? 'selectedWebList' : 'selectedWallList'][0].name;
        return `${state[status ? 'selectedWebList' : 'selectedWallList']?.length} ${status ? 'Websites' : 'Galleries'}`;
    };

    useEffect(() => {
        if (overViewList?.length && websiteList?.length && !selectedWebList.length) {
            setState(prev => ({ ...prev, selectedWallList: overViewList }));
            memoHandleWallChange(overViewList, true);
            addWallId(getIds(overViewList));
        }
    }, [overViewList, websiteList, selectedWebList]);

    useEffect(() => {
        if(activeTab ==5) setState(prev => ({ ...prev, selectedShopOn: wallList }));
    }, [wallList])

    useEffect(() => {
        if (overViewList?.length) {
            let wallIds = overViewWall;
            let webIds = state?.selectedWebList?.map(i => i.id);
            if (!overViewWall?.length) wallIds = [0]
            else wallIds = filterIds(wallIds, 'overViewWall')
            if (activeTab == 1) getOverView(wallIds, activeTab, webIds, shopOnWall, true);
            else if (activeTab == 5) getOverView(wallIds, activeTab, webIds, shopOnWall)
        }

    }, [overViewList, overViewWall, wallList, shopOnWall])

    return (
        <div className="subheader py-1 py-lg-0" style={!isShopify ? { marginTop: '50px' } : {}}>
            <Container fluid>
                <div className='f-between w-100'>
                    <div className="d-flex align-items-center flex-nowrap flex-shrink-0 me-3">
                        <Nav variant="underline" className='d-none d-lg-flex' activeKey={activeTab}>
                            <Nav.Item>
                                <Nav.Link eventKey="1">
                                    Overview
                                </Nav.Link>
                            </Nav.Item>
                            {shoppableStatus ? <Nav.Item>
                                <Nav.Link eventKey="5">
                                    Shopon.bio
                                </Nav.Link>
                            </Nav.Item> : null}
                        </Nav>
                        <Dropdown className='d-flex d-lg-none'>
                            <Dropdown.Toggle variant="secondary" className="btn-sm" id="analytics-tab">
                                {activeTab == 5 ? 'Shopon.bio' : 'Overview'}
                            </Dropdown.Toggle>

                            <Dropdown.Menu>
                                <NavDropdown.Item eventKey="1">Overview</NavDropdown.Item>
                                {shoppableStatus ? <NavDropdown.Item eventKey="5">Shopon.bio</NavDropdown.Item> : null}
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                    <div className="d-flex align-items-center flex-nowrap flex-shrink-0">
                        <Dropdown className='me-3 py-1' style={{ pointerEvents: `${isDisabled ? 'none' : ''}` }}>
                            <Dropdown.Toggle variant='secondary' size='sm' id="a_change_con" className='btn-min-w f-between d-flex'>{activeTab == 5 ? getShopOnDisplayText() : getDisplayText(false)}</Dropdown.Toggle>
                            {wallList?.length > 0 ? <Dropdown.Menu style={{ maxHeight: '300px', overflowY: 'auto' }}>
                                {wallList && wallList.map((item) => {
                                    const { name, id } = item;
                                    const selList = activeTab == 5 ? selectedShopOn : selectedWallList;
                                    const isChecked = selList?.some(i => i.id === id);
                                    return (
                                        <Dropdown.Item as="button" key={id} onClick={(e) => {
                                            e.stopPropagation();
                                            if (activeTab == 5) handleShopOnToggleSelect(item);
                                            else handleToggleSelect("selectedWallList", { id, name }, () => {
                                                memoHandleWallChange([item], !isChecked);
                                                memoChangeWall(item);
                                            });
                                        }}>
                                            <Form.Check
                                                type="checkbox"
                                                label={name}
                                                checked={isChecked}
                                                readOnly
                                            />
                                        </Dropdown.Item>
                                    );
                                })}
                            </Dropdown.Menu> : null}
                        </Dropdown>
                        {
                            activeTab == 1 ?
                                <Dropdown className='me-3 py-1' style={{ pointerEvents: isDisabled ? 'none' : '' }}>
                                    <Dropdown.Toggle variant='secondary' size='sm' id="multi_select_dropdown" className='btn-min-w f-between d-flex' disabled={selectedWebList?.length > 0 ? false : true}>
                                        {getDisplayText(true)}
                                    </Dropdown.Toggle>
                                    <Dropdown.Menu style={{ maxHeight: '300px', overflowY: 'auto' }}>
                                        {selectedWebList?.map((item, index) => {
                                            const id = item?.id
                                            const name = item?.name
                                            const isChecked = selectedWebList.some(i => i.id === id);
                                            return (
                                                <Dropdown.Item as="button" key={id} onClick={(e) => {
                                                    e.stopPropagation();
                                                    handleToggleSelect("selectedWebList", { id, name, wallId: item?.wallId }, () => { memoChangeWall(item) });
                                                }}>
                                                    <Form.Check type="checkbox" label={name} checked={isChecked} readOnly />
                                                </Dropdown.Item>
                                            );
                                        })}
                                    </Dropdown.Menu>
                                </Dropdown> : null
                        }

                        <div className='py-1' style={{ pointerEvents: `${isDisabled ? 'none' : ''}` }}>
                            <DateRangePicker onApply={handleOnApply} startDate={startDate} endDate={endDate} initialSettings={{
                                locale: { format: 'DD-MM-YYYY', separator: '-' },
                                maxDate: maxDate,
                                showDropdowns: true,
                                startDate: startDate, endDate: endDate,
                                ranges: {
                                    Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                                    Yesterday: [moment().subtract(1, 'days').format("DD-MM-YYYY"), moment().subtract(1, 'days').format("DD-MM-YYYY"),],
                                    'Last 7 Days': [moment().subtract(6, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                                    'Last 30 Days': [moment().subtract(30, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
                                    'Last Month': [moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"), moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),],
                                },
                            }} onCallback={handleDateRange}
                            >
                                <Button variant='secondary' aria-label='calendar' size='sm' id="date_range">
                                    <FontAwesomeIcon icon={'calendar-range'} iconClass={'me-1'} />
                                    {`${startDate} - ${endDate}`}
                                </Button>
                            </DateRangePicker>

                        </div>

                        {activeTab == 1 ? <button type="button" aria-label="Mode" className={`btn btn-secondary btn-sm ms-3 ${generatePDFLoader ? 'disabled' : ''}`} onClick={generatePDF}>
                            {
                                generatePDFLoader ? <span className="spinner-border spinner-border-sm align-middle ms-2 disabled"></span> : <FontAwesomeIcon icon={'download'} iconClass={'fa-duotone fa-solid me-0'} />
                            }
                        </button> : null}
                    </div>
                </div>
            </Container>
        </div>
    )
}

export default AnalyticsHeader