import React, { Component } from 'react';
import Author from './Author';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../utils';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { Image } from 'react-bootstrap';

class AuthorIcon extends Component {
       render() {
              const { networkId } = this.props;
              return (
                     <div className='position-relative me-2'>
                            <Author {...this.props}/>
                            <div className="position-absolute rounded-circle h-18px w-18px f-center bg-body" style={{padding: 3, right: 0, bottom:0}}>
                                   <Image className="object-center-contain" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(networkId)}.svg`)} height="18" width="18" alt={GET_NETWORK_NAME(networkId)} title={GET_NETWORK_NAME(networkId)} />
                            </div>
                     </div>
              );
       }
}

export default AuthorIcon;