import { Component } from "react";
import { connect } from "react-redux";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4plugins_wordCloud from "@amcharts/amcharts4/plugins/wordCloud";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";  
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import { Card } from "react-bootstrap";

class WordCloud extends Component{

    state = { word: this.props.word_cloud}
    componentDidMount() {
        const { wordCloud } = this.props;
        if (wordCloud?.length > 0) {
            this.setState({ word: wordCloud }, () => setTimeout(() => this.updateWord(), 100))
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.wordCloud !== prevProps.wordCloud) {
            const { wordCloud } = this.props;
            this.setState({ word: wordCloud }, () => setTimeout(() => this.updateWord(), 100))
        }
    }

    updateWord = () => { 
        const { wordCloud, id } = this.props;
        am4core.useTheme(am4themes_animated);
        let chart = am4core.create(id, am4plugins_wordCloud.WordCloud);
        let series = chart.series.push(new am4plugins_wordCloud.WordCloudSeries());
    
        series.accuracy = 4;
        series.step = 15;
        series.rotationThreshold = 0.7;
        series.maxCount = 200;
        series.minWordLength = 2;
        series.fontFamily = "inter, sans-serif";
        series.maxFontSize = am4core.percent(30);
        chart.logo.disabled = true;
        series.colors = new am4core.ColorSet();
        series.colors.passOptions = {}; // makes it loop
        series.angles = [0, -90];
        series.fontWeight = "600"
        series.dataFields.word = "tag";
        series.dataFields.value = "count";
        series.labels.template.propertyFields.fill = "color";
        series.labels.template.tooltipText = "{word}:\n[bold]{value}[/]";
        series.randomness = 0
        series.data = wordCloud;
    }

    render(){
        const {loader, wordCloud, id } = this.props
        const heading = window?.APP_CONTENT?.Analytics?.headings ?? {};
        return (
            <Card className='card-stretch gutter-b p-0 card-word-cloud'>
                <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                    <span className='fs-8 fw-semibold mb-0'>{heading?.word_cloud ?? "Word cloud"}</span>
                </div>
                <Card.Body style={{height:300}}>
                    {loader ? <span className="h-100 f-center text-gray-300 fs-5">{heading?.loading ?? ""}</span> : <>
                        <div className="word_cloud f-between justify-content-center h-100">
                            {(wordCloud && wordCloud.length > 0) ?
                                <div id={id} style={{ width: "100%", height: "300px", display: (wordCloud && wordCloud.length > 0) ? `block` : `none` }}></div>
                                : wordCloud && (wordCloud.length == 0 || wordCloud == null) ? 
                                <NoCardData title={heading?.no_data_found ?? ""} desc="" methodTitle={heading?.no_data_btn ?? ""} method={``} />
                            : null}
                        </div>
                    </>}
                </Card.Body>
            </Card>
        )
    }
}

export default WordCloud