import React, { useEffect, useMemo, useState } from 'react'
import { Card, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import NoCardData from '../../CommonComponents/NotFound/NoCardData';
import { useSelector } from 'react-redux';
import { AssetsbySkeleton } from './Skeleton';

const TopLanguages = () => {
  const { top_language, top_language_loader, flags } = useSelector((state) => state.analyticsOverView);
  const [topLang, setTopLang] = useState([]);

  const heading = useMemo(() => window?.APP_CONTENT?.Analytics?.headings ?? {}, []);

  const getFlagCode = (value, flags) => {
    let valueToSearch = String(value).toLowerCase();
    const item = flags.find(flag => (String(flag?.value)?.toLowerCase() == valueToSearch || String(flag?.value)?.toLowerCase().includes(valueToSearch)));
    return (value == 'Aragonese' || value == 'Asturian') ? 'es' :
      value == 'Breton' || value == 'French' || value == 'Interlingua' || value == 'Occitan' ? 'fr' :
        value == 'Central Kurdish' ? 'iq' :
          value == 'Faroese' ? 'de' :
            value == 'Norwegian Bokm' || value == 'Norwegian Nynorsk' ? 'no' :
              value == 'Romansh' ? 'ch' :
                value == 'Serbo' ? 'rs' :
                  value == 'Southern Sotho' ? 'za' :
                    value == 'Tagalog' ? 'ph' :
                      value == 'Tongan' ? 'nz' :
                        value == 'Walloon' ? 'be' :
                          value == 'Western Frisian' ? 'de' :
                            item ? String(item?.code)?.toLowerCase() : ''
  }

  useEffect(() => {
    let total = 0;
    let afterSeven = 0;
    for (let i = 0; i < top_language.length; i++) {
      const item = top_language[i];
      total += (item?.count ?? 0);
      if (i > 7) afterSeven += (item?.count ?? 0);
    }
    const list = [...top_language?.slice(0, 7), { language: 'Other', count: afterSeven }];
    setTopLang(list);
  }, [top_language]);
  return (
    <Card className="card-stretch gutter-b p-0 card-top-lang">
      <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
        <h3 className="fs-8 fw-semibold mb-0">{heading?.top_language ?? "Top Language"}</h3>
        <OverlayTrigger placement='top' overlay={<Tooltip>{heading?.post_overview_info ?? "Multiply your social UGC by collaborating with your top listed Creators"}</Tooltip>} >
          <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
        </OverlayTrigger>
      </div>
      <Card.Body style={{ height: '300px' }}>
        <div className="h-100 px-5 overflow-y-auto">
          {top_language_loader ? <AssetsbySkeleton /> : top_language.length
            ? topLang?.map((data, index) => {
              return (
                <div className="px-5 py-2 my-3 border rounded" key={index}>
                  <div className="d-flex align-items-center justify-content-between">
                    <div className="d-flex align-items-center me-10">
                      <div className="rounded me-3 d-flex">
                        <div className={`iti-flag ${getFlagCode(data.language, flags || [])}`}></div>
                      </div>
                      <div className="">
                        <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                          {data.language}
                        </p>
                      </div>
                    </div>
                    <p className="text-dark count-tag bg-transparent font-weight-boldest m-0">{data.count}</p>
                  </div>
                </div>
              );
            })
            : <NoCardData title={heading?.no_data_found ?? "No data found"} desc="" methodTitle="" method={''} />}
        </div>
      </Card.Body>
    </Card>
  )
}

export default TopLanguages