import { Component } from "react";
import { connect } from "react-redux";
import { Col, Card , OverlayTrigger, Row, Tooltip } from "react-bootstrap";
import { kFormatter } from "../../../../../utils";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";

class WebCard extends Component{
    render(){

        const { website, loader } = this.props
        const color = ['blue', 'orange', 'yellow', 'green', 'red']
        return <Row className="gx-4 web_tabs">
                    {website?.totalValues?.map((item, index) => {
                        const isPercentage = String(item.name).includes("%") && item.total ? `%` : ``
                        const isHours = String(item.name).includes("hours") && item.total ? item.total : ``
                        return <Col sm={6} lg={4} xl={3} key={`card${index}`}>
                            <Card className={`gutter-b card-shadow ${color[index]}`}>
                                <Card.Body>
                                    <div className="f-between">
                                        <p className="fs-8 fw-medium text-muted mb-2">
                                            {item.name}
                                        </p>
                                        <OverlayTrigger placement='top' overlay={<Tooltip>{item.tool_tip}</Tooltip>} >
                                            <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                        </OverlayTrigger>
                                    </div>
                                    <div className="d-flex align-items-center">
                                        <p className="h1 mb-0">{loader ? isHours ? '00:00:00' : '0' : isHours ? isHours : item.total ? kFormatter(item.total) : '0'}{isPercentage}</p>
                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>
                    })
                }
                </Row>
    }
}

const mapStateToProps=(state)=>{
    return {
        website: state.analyticsWebsite?.websiteOverview_data, 
        loader: state.analyticsWebsite?.websiteOverview_loader,  
    }
}

export default connect(mapStateToProps)(WebCard)