import React, { Component } from 'react';
import ReactApexChart from "react-apexcharts";
import moment from "moment";
import { debounce } from '../../../../../../utils';

const getNewState = (overViewData) => {
  return {
          series: overViewData,
          options: {
            colors: ["#546FED", "#EF45B1", "#F8B861"],
            chart: {
              id: "analyticsOverview",
              type: "line",
              animations: {
                enabled: true,
                easing: "linear",
                dynamicAnimation: {
                  speed: 1000,
                },
              },
              height: 350,
              zoom: { enabled: false },
              toolbar: { show: false },
              events: {
                mounted: () => {
                  let value = document.getElementsByClassName('apexcharts-legend apexcharts-align-center apx-legend-position-bottom')
                  if(value) {
                    if(value[0].children.length > 0){
                      overViewData?.forEach((item) => {
                        if(item.name == 'Total Visitors' && item.count > 0){
                          value[0].children[0].click()
                          value[0].children[2].click()
                        } else {
                          value[0].children[1].click()
                          value[0].children[2].click()
                        }
                      });
                    }
                  }
                },
              },
    
            },
            legend: {
              show: true,
              position: "bottom",
              itemMargin: {
                horizontal: 10,
                vertical: 10,
              },
              onItemClick: {
                toggleDataSeries: true
              },
              onItemHover: {
                highlightDataSeries: true // This controls the hover behavior; set to true/false based on preference
              }
            },
            dataLabels: { enabled: false },
            markers: { size: 0, style: "hollow" },
            stroke: {
              width: [4, 4, 4],
              curve: "straight",
              lineCap: 'square',  // Makes the line edges rounded
            },
            xaxis: {
              type: "datetime",
              tickAmount: 6,
              min: overViewData?.[0].data?.[0]?.[0],
              labels: { 
                rotate: -45,  // Rotate labels to avoid overlap
                formatter: (value) => {
                    return moment(value).subtract(1, 'days').format('DD MMM')  // Format as "Month Day"
                }
              } ,
              tooltip: {
                shared: true,
                intersect: true, // Allows tooltip to show when hovering near the points
                formatter: function (value) {
                  return moment(value).subtract(1, 'days').format('D-MM-YYYY') // Format as "Month Day"
                },
              },
            },
            yaxis: {
              labels: {
                formatter: function (value) {
                  return  value // Rounds the value to the nearest integer
                }
              }
            },
            grid: {
              show: true, // Enable grid
              borderColor: '#E8E8E8', // Grid line color
              strokeDashArray: 0, // Solid lines, or dashed lines by increasing the number
              position: 'back', // 'front' for grid lines in front of the chart
              xaxis: {
                lines: {
                  show: true, // Show vertical lines
                },
              },
              yaxis: {
                lines: {
                  show: true, // Show horizontal lines
                },
              },
            },
          },
          responsive: [
            {
              options: {
                chart: {
                  height: 300,
                },
              },
              options: {
                chart: {
                  height: 280,
                },
              },
            },
          ],
        }
}

class Chart extends Component {
    constructor(props) {
        super(props);
        this.chartRef = React.createRef();
        this.state = getNewState(props.record_overview);
      }

      updateGraph = debounce((overViewData) => {
        const newData = getNewState(overViewData);
        this.setState({ ...newData });
      }, 800)


      componentDidUpdate(prevProps) {
        if (prevProps.record_overview !== this.props.record_overview) this.updateGraph(this.props.record_overview);
      }
    
      render() {
        return <ReactApexChart ref={this.chartRef} options={this.state.options} series={this.state.series} type="line" height={350} />
      }
}

export default Chart 