import { Component } from "react";
import { Card, Nav, Tab } from "react-bootstrap";
import _ from "lodash";
import SentimentStatistics from "./component/SentimentStatistics";
import SentimentPost from "./component/SentimentPost";

class Sentiment extends Component {

    render() {
        const { statistics, posts, proUser } = this.props
        const content = window?.APP_CONTENT?.Analytics?.sentiment ?? {};
        const sentimentStatistics = content?.sentiments;
        const sentimentPosts = content?.sentiment_posts;
        return (
            <Tab.Container defaultActiveKey="statistics">
                <Card className='card-stretch p-0 card custom-tabalign'>
                    <Card.Body className='p-0'>
                        <>
                            <Nav variant="underline" className="flex-row px-3 px-sm-5 border-bottom">
                                <Nav.Item>
                                    <Nav.Link eventKey="statistics" className={'py-2 py-sm-3 fs-8'}>{sentimentStatistics?.heading_first ?? 'Sentiment statistics'}</Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="sposts" className={'py-2 py-sm-3 fs-8'}>{sentimentPosts?.heading ?? 'Sentiment posts'}</Nav.Link>
                                </Nav.Item>
                            </Nav>
                            <Tab.Content className="w-100 d-flex align-items-center" style={{ height: 'calc(100% - 45px)'}}>
                                <Tab.Pane eventKey="statistics" className="w-100 px-4">
                                    <SentimentStatistics {...{ statistics, proUser, sentimentText: sentimentStatistics }} />
                                </Tab.Pane>
                                <Tab.Pane eventKey="sposts" mountOnEnter className="w-100">
                                    <SentimentPost {...{ posts, sentimentPosts }} />
                                </Tab.Pane>
                            </Tab.Content>
                        </>
                    </Card.Body>
                </Card>
            </Tab.Container>
        )
    }
}

export default Sentiment