import { Component } from "react";
import { Button, Modal, Table, Row, Col, Image } from "react-bootstrap";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import { connect } from "react-redux";
import { GET_NETWORK_ICON, kFormatter } from "../../../../../utils";
import moment from "moment";
import BreakDownChart from "./BreakDownChart";
import Author from "../../CommonComponents/Author/Author";
import { baseUrl } from "../../../../../Assets/AssetsHelper";

class BreakDownModal extends Component {
    state = { colors: ['#008ffb', '#00e396', '#fe8900', '#2dbb74'] }

    render() {
        const { isPopupOn, onCloseModel, loader, breakDown, breakDownImage, authorized, breakDownItem } = this.props
        const { colors } = this.state
        return <Modal size="lg" show={isPopupOn} onHide={onCloseModel} centered>
            <Modal.Header className="" closeButton>
                <Modal.Title>Breakdown</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className='overflow-auto' style={{ height: 500 }}>
                    <div className="p-3">
                        <Row>
                            <Col sm={5}>
                                <ImageType mediaClass={'position-relative overflow-hidden w-100 mb-5'} size={100} height={200} width={200} url={breakDownImage} isCover={true} alt={""} />
                            </Col>
                            <Col sm={7}>
                                <div className="mb-5">
                                    <BreakDownChart colors={colors} className='donut-chart' cartLabel={['Post views', 'CTA clicks']} breakDownItem={breakDownItem} website={true} />
                                </div>
                                <div className="shopClickSection">
                                    <h5>Shop Clicks</h5>
                                    <Row>
                                        {breakDown?.recordVisitorCount && breakDown?.recordVisitorCount[0].labels.map((Item, index) => {
                                            return <Col xs={6} key={index}>
                                                <div className={`d-flex align-items-center justify-content-start`}>
                                                    <div className="h-12px w-12px rounded-circle me-2" style={{ backgroundColor: colors[index] }}></div>
                                                    <span className="fw-medium text-muted">{Item}</span>
                                                </div>
                                            </Col>
                                        })}

                                    </Row>
                                </div>
                            </Col>
                        </Row>
                    </div>
                    {breakDown && breakDown.productList && breakDown.productList.length ? <div className="viewBreakdown_table">
                        <Table size='lg' responsive>
                            <thead>
                                <tr>
                                    <th className="bg-light border-0">Posts</th>

                                    {authorized?.userData?.user_data?.sessionData?.createFrom && authorized?.userData?.user_data?.sessionData?.createFrom.toLowerCase() == 'shopify' ?
                                        <th className="bg-light border-0">Add to Cart</th>
                                        :
                                        <th className="bg-light border-0">CTA clicks </th>}
                                    <th className="bg-light border-0">Assisted</th>
                                </tr>
                            </thead>
                            <tbody>
                                {breakDown?.productList?.map((post, index) => {
                                    return <tr key={index}>
                                        <td className="align-middle">
                                            <div className="d-flex align-items-center">
                                                <div className="w-80px me-3">
                                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={80} width={80} isCover={true} url={post.postFileNew} alt={''} />
                                                </div>
                                                <div className="d-flex align-items-start flex-grow-1 w-100" style={{ maxWidth: 300 }}>
                                                    <Author imageSize={40}
                                                        symbolClass={'symbol-label'}
                                                        className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                        name={post.name} profileUrl={post.picture} networkId={28} />
                                                    <div className="d-flex text-start flex-column flex-grow-1">
                                                        <p className="w-75 text-nowrap text-ellipsis mb-0 h5">{post.name}</p>
                                                        <div className="d-flex align-items-center text-muted">
                                                            <span className="text-gray-400 fs-10 fw-medium text-ellipsis">@{post.username}</span>
                                                            <span className="h-4px w-4px rounded-circle mx-1 d-flex flex-shrink-0" style={{ backgroundColor: 'var(--bs-gray-400)' }}></span>
                                                            <span className="text-gray-400 fs-10 fw-medium text-ellipsis">{moment(post.postTime).format("DD-MMM-YYYY")}</span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <Image height={22} width={22} className='me-3' src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(post.networkId)}.svg`)} alt={''} fluid />
                                            </div>
                                        </td>
                                        <td className="align-middle">{(parseInt(post.ctaClick))}</td>
                                        <td className="align-middle">{post.discount_currency_symbol}{kFormatter(post.assisted)} </td>
                                    </tr>
                                })}

                            </tbody>
                        </Table>
                    </div> : <></>}
                </div>
            </Modal.Body>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        breakDown: state.analyticsConversion?.conversion_breackDown_data,
        loader: state.analyticsConversion?.conversion_breackDown_loader,
    }
}

export default connect(mapStateToProps)(BreakDownModal)