import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Col, Container, Row } from 'react-bootstrap';
import TopCounts from './TopCounts';
import OverviewChart from './OverViewChart';
import ActiveCreators from './ActiveCreators';
import TopPerformingPost from '../components/top-performance-posts/TopPerformingPost';
import TopAiTags from './TopAiTags';
import PostChart from '../ContentGallery/PostChart';
import WordCloud from '../ContentGallery/WordCloud';
import TopLanguages from './TopLanguages';
import PostOverview from './PostOverview';
import Recommendation from './Recommendation';
import Sentiment from './Sentiment';
import OverviewPdf from './OverviewPdf';

const loaderData = [
    { "name": "Post type", "labels": [ "Video posts", "Image posts", "Text posts" ] },
    { "name": "Post network", "labels": [ "Instagram Business", "Pinterest", "YouTube", "Instagram" ] }
]
class Overview extends PureComponent {
    render() {
        const { user, statistics, posts, activeCreators, topCreators, mostActiveUsersLoader, topCreatorsLoader, postNetwork, postLoader, wordCloud, wordCloudLoader, analyticsOverView, generatePDFLoader, content } = this.props
        const proUser = user?.truSense ?? 0

        const activeUsertotal = activeCreators?.length ?? 0;
        const contirbutorsTotal = topCreators?.length ?? 0;
        const heading = content?.headings;
        return(
            <>
                <Container fluid>
                    <Row className='gx-4 py-2'>
                        <TopCounts />
                        <Col className="mb-5" lg={{ span: 8, order: 1 }} >
                            <OverviewChart />
                        </Col>

                        <Col className="mb-5" lg={{ span: 8, order: 3 }}>
                            <Sentiment {...{ proUser, statistics, posts }} />
                        </Col>

                        <Col className="mb-5" md={6} lg={{ span: 4, order: 2 }}>
                            <Recommendation {...{ proUser }} />
                        </Col>

                        <Col className="mb-5" md={6} lg={{ span: 4, order: 4 }}>
                            <PostOverview />
                        </Col>

                        <Col lg={{ span: 12, order: 5 }}>
                            <TopPerformingPost />
                        </Col>
                        <Col lg={{ span: 8, order: 5 }}>
                            <ActiveCreators {...{ activeCreators, topCreators, mostActiveUsersLoader, topCreatorsLoader, analyticsOverView }} />
                        </Col>
                        <Col lg={{ span: 4, order: 5 }}>
                            <WordCloud {...{ wordCloud, loader: wordCloudLoader, id: "wordCloud1" }} />
                        </Col>

                        <Col lg={{ span: 8, order: 5 }}>
                            <PostChart {...{ postNetwork, loader: postLoader, loaderData }} />
                        </Col>
                        <Col lg={{ span: 4, order: 5 }}>
                            <TopLanguages />
                        </Col>
                    </Row>
                </Container>

                {generatePDFLoader ? <div className="analytics-pdf">
                    <OverviewPdf {...{ proUser, statistics, posts, activeUsertotal, contirbutorsTotal, activeCreators, topCreators, mostActiveUsersLoader, topCreatorsLoader, postNetwork, postLoader, loaderData, wordCloud, wordCloudLoader, analyticsOverView }} />
                </div> : null}
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        user: state.authorized?.userData?.user_data,
        statistics: state.analyticsOverView?.sentiment_by_post_statistics,
        posts: state.analyticsOverView?.sentiment_by_post,

        activeCreators: state.analyticsOverView?.most_active_users,
        mostActiveUsersLoader: state.analyticsOverView?.most_active_users_loader,
        topCreators: state.analyticsOverView?.top_creators,
        topCreatorsLoader: state.analyticsOverView?.top_creators_loader,

        postNetwork: state.analyticsContentGallery?.type_by_post_network,
        postLoader: state.analyticsContentGallery?.type_by_post_network_loader,

        wordCloud: state.analyticsContentGallery?.word_cloud,
        wordCloudLoader: state.analyticsContentGallery?.word_cloud_loader,
        analyticsOverView: state.analyticsOverView,
    }
}

export default connect(mapStateToProps)(Overview)