import React from 'react'
import { Nav, Tab } from "react-bootstrap";
import NoCardData from '../../../CommonComponents/NotFound/NoCardData';
import PositivePost from './PositivePost';

const SentimentPost = ({ posts, sentimentPosts }) => {
    return (
        posts ?
        <Tab.Container defaultActiveKey="positive">
            <Nav variant="underline" className="flex-row px-3 px-sm-5 border-bottom">
                <Nav.Item>
                    <Nav.Link eventKey="positive" className={'py-2 py-sm-3'}>{sentimentPosts?.tab_first??'Positive'}</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="negative" className='py-2 py-sm-3'>{sentimentPosts?.tab_second??'Negative'}</Nav.Link>
                </Nav.Item>
            </Nav>
            <Tab.Content>
                <Tab.Pane eventKey="positive" mountOnEnter>
                    <PositivePost {...{ posts : posts.positive }} />
                </Tab.Pane>
                <Tab.Pane eventKey="negative" mountOnEnter>
                    <PositivePost {...{ posts : posts.negative }} />
                </Tab.Pane>
            </Tab.Content>
        </Tab.Container> : <NoCardData title={sentimentPosts?.no_data ?? ''} desc="" methodTitle={sentimentPosts?.no_data_btn ?? ""} method={``} />
    )
}

export default SentimentPost