import { Component } from "react";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { Card, OverlayTrigger, Tooltip } from 'react-bootstrap';
import AreaChart from "../../CommonComponents/AreaChart";
class PostView extends Component{

    state = {clickData:null}

    componentWillMount(){
        const {loader, clickData} = this.props
        if(!loader){
            this.setState({clickData: clickData})
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.props.loader != nextProps.loader){
            this.setState({clickData: nextProps.clickData})
        }
    }


    render(){
        const { clickData } = this.state
        const { loader, content } = this.props
        const loaderData = {
            "series": [content || {}],
            "labels": [""]
        }
        const series = clickData?.series?.[1] || {}
        if(content?.name && content?.tool_tip) {
            series["name"] = content?.name ?? "";
            series["tool_tip"] = content?.tool_tip ?? "";
        }
        return <Card className="card-stretch gutter-b">
        <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
            <div className="d-flex align-items-center">
                <span className="h5 mb-0">{loader ? content?.name ?? "" : series?.name ?? ""}</span>
                <div className="ms-2">
                    <OverlayTrigger placement='top' overlay={<Tooltip>{loader ? content?.tool_tip ?? "" : series.tool_tip ?? ""}</Tooltip>} >
                        <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                    </OverlayTrigger>
                </div>
            </div>
            <span className="d-flex align-items-center text-muted">{loader ? content?.name ?? "" : series?.name ?? ""} <span className="display-4 fw-bold ms-2 ">{loader ? 0 : series.total}</span></span>
        </div>
        <Card.Body>
            <div className="chartArea"> 
                <AreaChart chartData={loader ? loaderData : clickData} shopOn={loader ? false :true} serialNum={0}/>
            </div>
        </Card.Body>
    </Card>
    }
}


const mapStateToProps=(state)=>{
    return {
        clickData: state.analyticsShopOn?.shopOnOverview_data,
        loader: state.analyticsShopOn?.shopOnOverview_loader
    }
}


export default connect(mapStateToProps) (PostView)