import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import Chart from './component/Chart';
import { OverviewSkeleton } from './Skeleton';


const getTimestamp = (dateStr) => {
    const [day, month, year] = dateStr.split("/");

    // Rearrange to MM/DD/YYYY format
    const formattedDate = `${month}/${day}/${year}`;

    // Create a new Date object and get the timestamp
    const timestamp = new Date(formattedDate).getTime();
    const unixTimestamp = Math.floor(timestamp);
    return unixTimestamp;
}

const dataConvertToInt = (data, label) => {
    if (data?.length > 0 && label?.length > 0) {
        let temp = [];
        for (let i = 0; i < data.length; i++) {
            temp.push([getTimestamp(label[i]), data[i]])
        }
        return temp;
    }
    return [];
}

export const OverviewChartFormatting = (formattingData) => {
    const data = [];
    if (formattingData?.length > 0) {
        formattingData.map((item) => {
            data.push({
                name: item.name,
                type: 'line',
                data: dataConvertToInt(item.data, item.labels),
                count: item.count
            })
        })
    }
    return data;
}
class OverviewChart extends PureComponent {

    state = {
        overview: null
    }

    componentWillMount() {
        const { loader, overview } = this.props
        if (!loader && overview) {
            this.setState({ overview: overview })
        }
    }

    componentWillReceiveProps(nextProps) {
        if (this.props.loader != nextProps.loader && nextProps.overview) {
            this.setState({ overview: nextProps.overview })
        }
    }

    render() {
        const { overview } = this.state;
        const heading = window?.APP_CONTENT?.Analytics?.headings ?? {};
        
        return (
            <div className="card card-custom card-stretch h-100 border-0 custom-overview">
                <div className="card-header min-h-auto py-3">
                    <h3 className="h5 mb-0">{heading?.overview ?? "Overview"}</h3>
                </div>
                <div className="card-body">
                    {overview && overview.length > 0 ? <Chart record_overview={OverviewChartFormatting(overview)} /> : <OverviewSkeleton />}
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        overview: state.analyticsOverView.overview,
        loader: state.analyticsOverView?.overview_loader,
    }

}

export default connect(mapStateToProps)(React.memo(OverviewChart))