import { Component } from "react";
import { connect } from "react-redux";
import { Button, Card, Table, OverlayTrigger, Tooltip, Image } from "react-bootstrap";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { Loader } from "../../CommonComponents/Loader";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import AuthorIcon from "../../CommonComponents/Author/AuthorIcon";
import { kFormatter } from "../../../../../utils";
import moment from "moment";
import ErrorFound from "../../../../ErrorFound";
import BreakDownModal from "./BreakDownModal";


const ItemCell = (props) => <th className={`fw-bold text-nowrap ${props.className?props.className:''}`} style={{ minWidth: props.width }}>{props.children}</th>
class WebPosts extends Component {
    state = { currentPage: 1, showPost: 10, pages: 1, allFeeds: [], isPopupOn: false, newPagination: [], pageData: [], breakDownImage: '', sortBy: null, addToCart: false, productView: false, breakDownItem: '', productData: [] }

    componentDidMount() {
        const { loader, postData } = this.props
        if (!loader) {
            this.setState({ productData: postData }, () => this.countPages())
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.loader != this.props.loader) this.setState({ productData: nextProps.postData }, () => this.countPages())
    }

    sortProduct = () => {
        const { postData } = this.props;
        const sortedData = [...postData];
        if (this.state.productView) {
            sortedData.sort((a, b) => a.viewCount - b.viewCount);
        } else {
            sortedData.sort((a, b) => b.viewCount - a.viewCount);
        }
        this.setState({ productData: sortedData, sortBy: 'productView', productView: !this.state.productView }, () => this.countPages());
    }


    countPages = () => {
        const { productData } = this.state;
        var i = 0, products = [], n = productData?.length
        while (i < n) { products.push(productData?.slice(i, i += this.state.showPost)) }
        setTimeout(this.setState({ pageData: products, totalPage: products.length }, () => {
            const pagination = this.generatePagination(this.state.currentPage, products.length);
            this.setState({ newPagination: pagination })
        }), 100)
    }

    generatePagination = (currentPage, totalPages) => {
        const pagination = [];
        pagination.push(1);
        if (currentPage > 5) {
            pagination.push('...');
            for (let i = currentPage - 2; i <= currentPage; i++) {
                if (i > 1 && i < currentPage) {
                    pagination.push(i);
                }
            }
        } else {
            for (let i = 2; i <= currentPage; i++) {
                pagination.push(i);
            }
        }

        pagination.push(currentPage);
        if (currentPage < totalPages - 2) {
            for (let i = currentPage + 1; i <= currentPage + 2; i++) {
                if (i < totalPages) {
                    if (i != 0) {
                        pagination.push(i);
                    }
                }
            }
            pagination.push('...');
        } else {
            for (let i = currentPage + 1; i < totalPages; i++) {
                pagination.push(i);
            }
        }
        pagination.push(totalPages);

        var arr = pagination.filter(function (item) { return item !== 0 })

        return [...new Set(arr)];
    }

    sortCart = () => {
        const { postData } = this.props;
        const sortedData = [...postData];
        if (this.state.addToCart) {
            sortedData.sort((a, b) => a.ctaClick - b.ctaClick);
        } else {
            sortedData.sort((a, b) => b.ctaClick - a.ctaClick);
        }
        this.setState({ productData: sortedData, sortBy: 'addToCart', addToCart: !this.state.addToCart }, () => this.countPages());
    }

    breakDownProduct = (item) => {
        this.setState({ breakDownImage: item.postFileNew })
        this.setState({ breakDownItem: item })
    }

    changePage = (x) => {
        const { pageData } = this.state;
        const pagination = this.generatePagination(x, pageData.length);
        this.setState({ currentPage: x, newPagination: pagination })
    }

    nextPrev = (no) => {
        this.setState({ currentPage: no })
        const pagination = this.generatePagination(no, this.state.pageData.length);
        this.setState({ currentPage: no, newPagination: pagination })
    }

    render() {
        const { loader, postData, userData } = this.props
        const { addToCart, productView, pageData, currentPage, isPopupOn, breakDownImage, breakDownItem, newPagination, pages, totalPage } = this.state
        return <>
        <Card className='border-none mb-5'>
                    <Card.Body className='py-0 px-0 overflow-auto' style={{minHeight:'calc(100vh - 350px)'}}>
                        <Table className='pb-3' size='lg' responsive>
                            <thead className='px-3 thead-light'>
                                <tr className='px-3'>
                                    <ItemCell width={100}></ItemCell>
                                    <ItemCell width={150}>Posts</ItemCell>
                                    <ItemCell width={150}># of products </ItemCell>
                                    <ItemCell width={100} className='cursor-pointer' onClick={() => this.sortProduct()}>
                                        Post views
                                        <span className={'ms-2 text-muted'}><FontAwesomeIcon icon={'arrow-up-arrow-down'}/></span>
                                    </ItemCell>
                                    <ItemCell width={100} onClick={() => this.sortCart()}>{userData?.sessionData?.createFrom && userData?.sessionData?.createFrom.toLowerCase() == 'shopify' ? 'Add to Cart' : 'CTA clicks'}</ItemCell>
                                    <ItemCell width={100}>Assisted revenue</ItemCell>
                                    <ItemCell width={50}></ItemCell>
                                </tr>
                            </thead>
                            <tbody>
                                {loader ?
                                    <tr>
                                        <td className='border-0' colSpan="10" style={{ textAlign: 'center' }}>
                                            <div style={{minHeight:'calc(100vh - 500px)' }}>
                                                <Loader />
                                            </div>
                                        </td>
                                    </tr>
                                    :
                                    pageData && pageData.length && pageData[currentPage - 1] && pageData[currentPage - 1].length ? pageData[currentPage - 1].map((Item, index) => {
                                        return <tr key={index}>
                                            <td className="align-middle">
                                                <div className="d-flex align-items-center">
                                                    <div className="w-60px me-3">
                                                        <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={60} width={60} url={Item.postFileNew} isCover={true} alt={Item.product_title}/>
                                                    </div>
                                                    <div className="overflow-hidden">
                                                        <span className="d-block fs-8 fw-semibold text-ellipsis" style={{ maxWidth: 200 }}>{Item.product_title}</span>
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="align-middle">
                                                <div className="d-flex align-items-center">
                                                <AuthorIcon imageSize={42} 
                                                    symbolClass={'symbol-label fw-bolder text-uppercase fs-5'} 
                                                    className={'symbol symbol-42 rounded-circle flex-shrink-0 me-2 overflow-hidden'} 
                                                    name={Item.name} profileUrl={Item.picture} networkId={Item.networkId} />
                                                    <div className="d-flex text-left flex-column conn_user_info w-75">
                                                        <p className="w-75 text-nowrap text-ellipsis mb-0 h5">{Item.name}</p>
                                                        <div className="d-flex align-items-center text-muted">
                                                            <span className="text-gray-400 fs-10 fw-medium text-ellipsis">@{Item.name}</span>
                                                            <span className="h-4px w-4px rounded-circle mx-1 d-flex flex-shrink-0" style={{backgroundColor:'var(--bs-gray-400)'}}></span>
                                                            <span className="text-gray-400 fs-10 fw-medium text-ellipsis">{moment(new Date(Item.createdAt * 1000)).format('DD-MMM-yyy')}</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </td>
                                            <td className="align-middle">
                                                <p className="fw-bold text-nowrap mb-0" style={{maxWidth:400}}>
                                                    {Item.products ?
                                                    <div className="symbol-group symbol-hover flex-wrap">
                                                        {Item.products.map((Item, Index) => {
                                                        return <span key={Index}>
                                                                    <OverlayTrigger placement="top" overlay={<Tooltip className="text-start">
                                                                        <p className="text-gray-100 mb-0 w-100 fw-bold text-ellipsis text-start">{Item.product_title}</p>
                                                                            <p className="mb-0 text-ellipsis mb-0 fs-8 text-start">
                                                                                Price : <strong>{Item.ncy}{Item.price}</strong>
                                                                            </p>
                                                                            <p className="mb-0 text-ellipsis mb-0 fs-8 text-start">
                                                                                CTA Click : <strong>{Item.ctaClick}</strong>
                                                                            </p>
                                                                        </Tooltip>}>
                                                                        <div className="symbol symbol-40 mb-1">
                                                                            <ImageType mediaClass={'rounded-circle border bg-body w-100'} size={100} height={40} width={40} isCover={true} url={Item.product_image_new} alt={Item.product_title} isProduct={true}/>
                                                                        </div>
                                                                        </OverlayTrigger>
                                                                    </span>
                                                                })}
                                                    </div>
                                                    : ' -- ' }</p>
                                            </td>
                                            <td className="align-middle"> {Item.viewCount} </td>
                                            <td className="align-middle"> {Item.ctaClick} </td>
                                            <td className="align-middle"> {Item.currency}{kFormatter(Item.assisted)}</td>
                                            <td className="text-end align-middle"> 
                                                <Button variant="secondary" size='sm' className="text-nowrap" onClick={() => this.setState({ isPopupOn: true }, () => this.breakDownProduct(Item))}>
                                                    View breakdown
                                                </Button>
                                            </td>
                                        </tr>
                                    })
                                        :
                                        <tr>
                                            <td className='border-0' colSpan="10" style={{ textAlign: 'center' }}>
                                                <div className="f-center" style={{ minHeight: 'calc(100vh - 350px)' }}>
                                                    <NoCardData title="No data found" desc="" methodTitle="" method={''} />
                                                </div>
                                            </td>
                                        </tr>
                                }
                            </tbody>
                        </Table>
                        {isPopupOn ? <ErrorFound><BreakDownModal breakDownItem={breakDownItem} onCloseModel={() => this.setState({ isPopupOn: false })} isPopupOn={isPopupOn} breakDownImage={breakDownImage} userData={userData} key={Math.random() * 8} /></ErrorFound> : ''}

                        {postData && postData && postData.length > 10 ? 
                        <div className="p-4">
                            <ul className="pagination pagination-sm mb-0">
                                <li className="page-item">
                                    <span className={`page-link cursor-pointer ${currentPage == 1 ? ' disabled' : ' cursor-pointer'}`} onClick={() => { this.nextPrev(currentPage - 1) }}>
                                        <FontAwesomeIcon icon={'angle-left'}/>
                                    </span>
                                </li>

                                {newPagination && newPagination.length ? newPagination.map((pageNo, i) =>
                                <li className={`page-item ${currentPage == pageNo ? 'active' : ' cursor-pointer'}`}>
                                    <span className={`page-link`} key={i} onClick={() => this.changePage(pageNo)}> {pageNo} </span>
                                </li>
                                )
                                :
                                <li className="page-item"><span className="page-link disabled">1</span></li>
                                }
                                <li className="page-item">
                                    <span className={`page-link cursor-pointer ${currentPage == totalPage ? ' disabled' : ' cursor-pointer'}`} onClick={() => { this.nextPrev(currentPage + 1) }}>
                                        <FontAwesomeIcon icon={'angle-right'}/>
                                    </span>
                                </li>
                            </ul> 
                        </div>
                        : null}
                    </Card.Body>
                </Card>


                </>
    }


}

const mapStateToProps = (state) => {
    return {
        postData: state.analyticsWebsite?.websitePost_data,
        loader: state.analyticsWebsite?.websitePost_loader,
        userData: state.authorized?.userData?.user_data
    }
}

export default connect(mapStateToProps)(WebPosts)