import { Component } from "react";
import { Button, Card, Image } from "react-bootstrap";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import ReactHtmlParser from 'react-html-parser';
import moment from "moment";
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import ShowMoreContent from "../../CommonComponents/ShowMoreContent";
import Author from "../../CommonComponents/Author/Author";
import { GET_NETWORK_ICON } from "../../../../../utils";

class MentionChart extends Component{

    render(){
        const { posts, loader } = this.props
        return (
            <Card className='card-stretch gutter-b p-0 card-mention-chart'>
                <div className="d-flex align-items-center p-3 py-2 py-sm-3 p-sm-5 border-bottom f-between">
                    <span className='fs-8 fw-semibold mb-0'>Mention posts</span>
                    { !loader && (posts[0]?.mentionsPosts && posts[0]?.mentionsPosts.length > 5) ? <Link className='btn-link fs-8 text-end' to={'/creators'} title="creators">View All</Link> : null}
                </div>
                <Card.Body style={{height:380}} className="overflow-auto">
                    {loader ? <span className="h-100 f-center text-gray-300 fs-5">Loading...</span> :
                        
                            posts[0].mentionsPosts && posts[0].mentionsPosts.length > 0 ? posts[0].mentionsPosts.map((item, index) => {
                            return <div className="d-flex align-items-center px-5 px-xxl-5 py-3 cursor-pointer flex-shrink-0 border-bottom" key={index}>
                                <div className="h-100px w-100px me-3 d-flex flex-shrink-0">
                                    <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={100} width={100} isCover={true} url={item.postFileNew}  alt={''} />
                                </div>
                                <div className="d-flex flex-column flex-grow-1">
                                    <div className="d-flex flex-grow-1 mb-1 w-100" onClick={()=> item?.link ? window.open(item.link, '_blank') : {}}>                                                                    
                                        <div className="d-flex align-items-start flex-grow-1 w-100" >
                                        <Author imageSize={40} 
                                                symbolClass={'symbol-label'} 
                                                className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'} 
                                                name={item.author.name} profileUrl={item.author.image} networkId={''} />
                                            <div className="d-flex text-start flex-column flex-grow-1">
                                                <p className="w-75 text-nowrap text-ellipsis mb-0 h5">{item.author.name}</p>
                                                <div className="d-flex align-items-center text-muted">
                                                    <span className="text-gray-400 fs-9 fw-medium text-ellipsis">@{item.author.username}</span>
                                                    <span className="h-4px w-4px rounded-circle mx-1 d-flex flex-shrink-0" style={{backgroundColor:'var(--bs-gray-400)'}}></span>
                                                    <span className="text-gray-400 fs-9 fw-medium text-ellipsis">{moment(item.postTime).format("DD-MMM-YYYY")}</span>
                                                </div>
                                            </div>
                                        </div>
                                        <Image height={22} width={22} className='me-3' src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.network.id)}.svg`)} alt={''} fluid />
                                    </div>

                                    <div className="d-flex flex-column flex-grow-1">
                                        <ShowMoreContent classes={'mb-3'} content={ReactHtmlParser(item.content)} idIndex={item.id} line={2} />
                                    </div>
                                </div>
                            </div>
                            }) 
                            : 
                            <NoCardData title="No data found" desc="" methodTitle="" method={``} />
                    }
                </Card.Body>    
            </Card>
        )
    }
}

const mapStateToProps = (state) => {
    return { 
        posts: state.analyticsContentGallery?.post_by_mention,
        loader: state.analyticsContentGallery?.post_by_mention_loader
    }
}

export default connect(mapStateToProps)(MentionChart)