import { Component } from "react";
import { connect } from "react-redux";
import { Button, Card, Table } from "react-bootstrap";
import { kFormatter, shopifyApp } from "../../../../../utils";
import ErrorFound from "../../../../ErrorFound";
import { getConversionBreackData } from "../../../../../actions/analytics";
import BreakDownModal from "./BreakDownModal";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { Loader } from "../../CommonComponents/Loader";
import ImageType from "../../CommonComponents/MediaType/ImageType";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";

const ItemCell = (props) => <th className={`fw-bold text-nowrap ${props.className?props.className:''}`} style={{ minWidth: props.width }} onClick={()=>props.method ? props.method() : {}}>{props.children}</th>
class ProductList extends Component{
    
    state={currentPage:1, showPost:10, pages:1, allFeeds:[], isPopupOn:false, newPagination: [], pageData:[], breakDownImage:'', sortBy: null, addToCart:false , productView:false , breakDownItem:'', productData:[]}

    componentDidMount(){
        const { loader, conversion } = this.props
        if(!loader){
            this.setState({productData:conversion.list},()=>this.countPage())
        } 
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.loader != this.props.loader) this.setState({productData:nextProps.conversion.list},()=>this.countPage())
    }

    countPage=()=>{ 
        const { productData } = this.state;
        var i = 0, products = [], n = productData?.length
        while (i < n) { products.push(productData.slice(i, i += this.state.showPost)) }
        setTimeout(this.setState({ pageData: products, pages: products.length },()=>{
            const pagination = this.generatePagination(this.state.currentPage, products.length);
            this.setState({ newpagination: pagination })
        }), 100)
    }

    generatePagination = (currentPage, totalPages) => {
        const pagination = [];
        pagination.push(1);
        if (currentPage > 3) {
            pagination.push('...');
            for (let i = currentPage - 2; i <= currentPage; i++) {
                if (i > 1 && i < currentPage) {
                    pagination.push(i);
                }
            }
        } else {
            for (let i = 2; i <= currentPage; i++) {
                pagination.push(i);
            }
        }

        pagination.push(currentPage);
        if (currentPage < totalPages - 2) {
            for (let i = currentPage + 1; i <= currentPage + 2; i++) {
                if (i < totalPages) {
                    if (i != 0) {
                        pagination.push(i);
                    }
                }
            }
            pagination.push('...');
        } else {
            for (let i = currentPage + 1; i < totalPages; i++) {
                pagination.push(i);
            }
        }
        pagination.push(totalPages);

        var arr = pagination.filter(function (item) { return item !== 0 })

        return [...new Set(arr)];
    }

    sortProduct = () => {
        const { conversion } = this.props;
        const {productView} = this.state
        const sortedData = [...conversion?.list];
        if (productView) {
            sortedData.sort((a, b) => a.view - b.view);  
            } else {
            sortedData.sort((a, b) => b.view - a.view);  
            } 
        this.setState({ productData:sortedData, sortBy: 'productView', productView:!productView }, ()=>this.countPage());
    };

    sortCart = () => {
        const { conversion } = this.props;
        const {addToCart} = this.state
        const sortedData = [...conversion?.list];
        
        if (addToCart) {
            sortedData.sort((a, b) => a.ctaClick - b.ctaClick);  
        } else {
            sortedData.sort((a, b) => b.ctaClick - a.ctaClick);  
        }
        
        this.setState({ productData:sortedData, sortBy: 'addToCart' , addToCart:!addToCart}, () => this.countPage());
    };

    breakDownProduct =(item)=>{
        this.props.getBreakDown(item.id)
        this.setState({breakDownImage: item.product_image, breakDownItem:item, isPopupOn:true})
        
    }

    render(){
        const {loader, conversion, userData} = this.props
        const { pageData, currentPage, isPopupOn, breakDownItem, breakDownImage} = this.state
        const isShopify = shopifyApp();
        return  <Card className='border-none mb-5'>
                    <Card.Body className='py-0 px-0' style={{minHeight:'calc(100vh - 350px)'}}>
                        <Table className='pb-3' size='lg' responsive >
                            <thead className='px-3 thead-light'>
                                <tr className='px-3'>
                                    <ItemCell width={300}>Product (Top 5)</ItemCell>
                                    <ItemCell width={140}>Price</ItemCell>
                                    <ItemCell width={140}>SKU</ItemCell>
                                    <ItemCell className='cursor-pointer' width={140} method={this.sortProduct}>
                                        Product views
                                        <span className={'ms-2 text-muted'}><FontAwesomeIcon icon={'arrow-up-arrow-down'}/></span>
                                    </ItemCell>
                                    <ItemCell width={140} method={this.sortCart}>{userData?.sessionData?.createFrom && userData?.sessionData?.createFrom.toLowerCase() == 'shopify' ? 'Add to Cart':'CTA clicks'}</ItemCell>
                                    <ItemCell width={140}>Assisted revenue</ItemCell>
                                    <ItemCell width={140}></ItemCell>
                                </tr>
                            </thead>
                            <tbody>
                            {loader ? 
                                <tr>
                                    <div className='h-100'><Loader/></div>
                                </tr>
                                :
                                pageData && pageData.length && pageData[currentPage - 1] && pageData[currentPage - 1].length ? pageData[currentPage - 1].map((item, index) => { return <tr key={index}>
                                    <td>  
                                        <div className="d-flex align-items-center mr-2">
                                        <div className="symbol symbol-50 rounded-2 flex-shrink-0 me-3 overflow-hidden">
                                                <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={50} width={50} url={item.product_image} alt={item.product_title} isCover={true} isProduct={true}/>
                                            </div>
                                            <div className="overflow-hidden">
                                                <span className="d-block fs-8 fw-semibold text-ellipsis" style={{ maxWidth: 200 }}>{item.product_title}</span>
                                            </div>
                                        </div> 
                                    </td> 
                                    <td> <span> {item.discount_currency_symbol}  {kFormatter(item.price)} </span></td>
                                    <td> <span> {item.product_sku} </span></td>
                                    <td> <span> {item.view} </span></td>
                                    <td> <span> {item.ctaClick} </span></td>
                                    <td> <span> {item.discount_currency_symbol}  {kFormatter(item.assisted)}</span></td>
                                    <td className="text-end"> 
                                        <Button variant="secondary" size='sm' className="text-nowrap" disabled={item.ctaClick > 0 || item.assisted > 0 ? false :true } onClick={()=>{ this.breakDownProduct(item)}}>
                                            View breakdown
                                        </Button> 
                                    </td>
                                </tr>})
                                :
                                <tr>
                                    <td className='border-0' colSpan="10" style={{ textAlign: 'center'}}>
                                        <div className="f-center" style={{minHeight:'calc(100vh - 450px)'}}>
                                            <NoCardData title={'No data found'} />
                                        </div>
                                    </td>
                                </tr>
                            }
                            </tbody>
                        </Table>
                        {isPopupOn ? <ErrorFound><BreakDownModal {...this.props} onCloseModel={()=>this.setState({isPopupOn:false})} isPopupOn={isPopupOn} breakDownImage={breakDownImage} breakDownItem={breakDownItem}/></ErrorFound>:''}
                    </Card.Body>
                </Card>
    }
    
}

const mapStateToProps=(state)=>{
    return {
        conversion: state.analyticsConversion?.conversion_data, 
        loader: state.analyticsConversion?.conversion_loader,
        userData:state.authorized?.userData?.user_data,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getBreakDown : (id) => dispatch(getConversionBreackData(id,0))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductList)