import React from 'react';
import { Tab } from 'react-bootstrap';
import DonutChart from '../../../CommonComponents/DonutChart';
import { GET_NETWORK_COLOR } from '../../../../../../utils';

const PostNetwork = ({postNetwork, loader, loaderData}) => {
    
    return (
        <Tab.Pane eventKey="postNetwork" className='p-4'>
            <DonutChart chartData={loader && loaderData ? [loaderData[1]] : postNetwork && postNetwork[1] ? [postNetwork[1]] : [{ name: 'Post Network', labels: [], data: [0] }]} colors={postNetwork ? postNetwork[1].colors : null} labelList={postNetwork ? postNetwork[1].labels : null} hideLegend={true} height={300} />
            {!loader ? <div className="w-100 mt-4">
                <div className="d-flex align-items-center justify-content-center w-100 flex-wrap">
                    {
                        postNetwork && postNetwork[1] && postNetwork[1].labels && postNetwork[1].labels.length > 0 ? postNetwork[1].labels.map((lItem, index) => <div key={Math.random() * (33 + index)} className="d-flex flex-column text-center mx-2 mb-2">
                            <p className="mb-2 fs-8 fw-medium text-muted">{lItem}</p>
                            <h3 className="text-center h2">{postNetwork[1].data[index]}</h3>
                            <div className="h-2px w-100" style={{ backgroundColor: GET_NETWORK_COLOR(postNetwork[1].networkId) }}></div>
                        </div>) : null
                    }
                </div>
            </div> : null}
        </Tab.Pane>
    )
}

export default PostNetwork