import { Component } from "react";
import { connect } from "react-redux";
import { OverlayTrigger, Tooltip, Card } from "react-bootstrap";
import { GET_NETWORK_NAME, GET_NETWORK_ICON } from "../../../../../utils";
import ShowMoreContent from "../../CommonComponents/ShowMoreContent";
import { baseUrl } from "../../../../../Assets/AssetsHelper";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import Author from "../../CommonComponents/Author/Author";
import { kFormatter } from "../../../../../utils";
import TopPostSkel from "../Skeletons/TopPostSkel";
import ImageType from "../../CommonComponents/MediaType/ImageType";

class TopPosts extends Component{
    render(){
        const { postData, loader, content } = this.props
        return <Card className="card-stretch gutter-b">
        <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
        <span className="h5 mb-0">{content?.top_post ?? ""}</span>
        </div>
        <Card.Body className="overflow-auto" style={{height:500}}>
                {!loader ?
                    postData && postData.length > 0 ?
                        postData.map((item, index) => {
                            const { networkId, postFileNew, postcontent, picture, name, username, viewCount, ctaClick } = item;
                            return <div key={index} className="border-bottom mb-2 p-2" data-id={item.id}>
                                    <div className="d-flex">
                                        {postFileNew ? <div className="d-flex flex-shrink-0 flex-column cursor-pointer me-3" style={{width:120}}>
                                            <ImageType mediaClass={'position-relative overflow-hidden w-100 rounded-1 border'} size={100} height={200} width={200} url={postFileNew} isCover={true} alt={''}/>
                                        </div> : null}
                                        <div className="d-flex flex-grow-1 flex-column w-100 overflow-hidden">
                                            <div className="d-flex align-items-start mb-2">
                                                <div className="d-flex align-items-start me-2 flex-grow-1 overflow-hidden text-decoration-none">
                                                    <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                        imageSize={40}
                                                        symbolClass={'symbol-label'}
                                                        name={name} profileUrl={picture} networkId={networkId} />
                                                    <div className="pe-2 w-100 overflow-hidden">
                                                        <p className="mb-0"><strong className="text-break text-ellipsis w-100 d-block">{name}</strong></p>
                                                        <p className="mb-1 text-gray-500 text-break fs-9">@{username}</p>
                                                    </div>
                                                </div>  
                                                <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                                    <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(networkId)}</Tooltip>}>
                                                        <img className="img-fluid h-100" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(networkId)}.svg`)} height={20} width={20} alt={networkId} />
                                                    </OverlayTrigger>
                                                </span>
                                            </div>
                                            <div className="mt-lg-0 mt-2 w-100 overflow-hidden">
                                                <div className="row">
                                                    <div className="col-sm-7">
                                                        <ShowMoreContent classes={'mb-3'} content={postcontent} idIndex={item.id} line={2} />
                                                    </div>
                                                    <div className="col-sm-5 justify-content-end">
                                                        <div className="ms-0 ms-sm-auto" style={{ maxWidth: 340 }}>
                                                            <div className="d-flex align-items-center justify-content-between py-1">
                                                                <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                                {content?.post_click ?? ""}:</span>
                                                                <h6 className="fs-8 mb-0">{kFormatter(viewCount?viewCount:0)}</h6>
                                                            </div>
                                                            <div className="d-flex align-items-center justify-content-between py-1">
                                                                <span className="d-flex align-items-center fs-8 text-gray-500 fw-medium text-ellipsis me-2">
                                                                {content?.click_cta ?? ""}:</span>
                                                                <h6 className="fs-8 mb-0">{kFormatter(ctaClick?ctaClick:0)}</h6>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                            </div>
                        })
                        : <NoCardData title={content?.no_data ?? ""} desc="" methodTitle="" method={''} />
                        : <TopPostSkel postCount={4} />}

        </Card.Body>
    </Card>

    }
}


const mapStateToProps=(state)=>{
    return { 
        postData: state.analyticsShopOn?.shopOnTopPost_data,
        loader: state.analyticsShopOn?.shopOnTopPost_loader,
    }
}

export default connect(mapStateToProps) (TopPosts)