import React from 'react'
import { Card, Image } from "react-bootstrap";
import ReactHtmlParser from 'react-html-parser'
import Author from '../../../CommonComponents/Author/Author';
import { baseUrl } from '../../../../../../Assets/AssetsHelper';
import NoCardData from '../../../CommonComponents/NotFound/NoCardData';
import ShowMoreContent from '../../../CommonComponents/ShowMoreContent';

const PositivePost = ({posts}) => {
    return (
            <Card className='card-stretch gutter-b p-0 shadow-none border-0 card-sentiment'>
                <Card.Body className='p-0 overflow-auto' style={{ height: 356 }}>
                    {
                        posts && posts?.length > 0 ? posts?.map((item, index) => {

                            return <div key={index} className="d-flex align-items-center px-5 px-xxl-5 py-3 cursor-pointer flex-shrink-0 border-bottom">
                                <Author className={'symbol symbol-40 rounded-circle flex-shrink-0 me-3 overflow-hidden'}
                                    imageSize={40}
                                    symbolClass={'symbol-label'}
                                    name={item?.username} profileUrl={item.picture.replace(/\s/g, '')} networkId={''} />
                                <Image height={22} width={22} className='me-3' src={baseUrl(`media/icons/social-icons/${item?.icon && item?.icon.includes('fa-') ? item?.icon.replace('fa-', '') : ''}.svg`)} alt={''} fluid />
                                <div className="d-flex flex-column flex-grow-1">
                                    <ShowMoreContent classes={'mb-3'} content={ReactHtmlParser(item.content)} idIndex={item.id} line={3} />
                                </div>
                            </div>
                        })
                            :
                            <NoCardData title="No data found" desc="" methodTitle="" method={``} />
                    }
                </Card.Body>
            </Card>
    )
}

export default PositivePost