import { Component } from "react";
import { Col, Container, Row } from "react-bootstrap";
import PageView from "./PageView";
import PostView from "./PostView";
import ButtonView from "./ButtonView";
import ClickView from "./ClickView";
import TopPosts from "./TopPosts";
import PopularProduct from "./PopularProduct";

class Shopon extends Component{
    render(){
        const { content } = this.props;
        return <Container fluid>
            <Row className="gx-4">
                <Col lg={6} sm={12}>
                    <PageView content={content?.page_view ?? {}}/>
                </Col>
                <Col lg={6} sm={12}>
                    <PostView content={content?.post_view ?? {}}/>
                </Col>
            </Row>
            <Row className="gx-4">
                <Col lg={6} sm={12}>
                    <ButtonView content={content?.button_view ?? {}}/>
                </Col>
                <Col lg={6} sm={12}>
                    <ClickView content={content?.click_view ?? {}}/>
                </Col>
            </Row>
            <Row className="gx-4">
                <Col lg={6} sm={12}>
                    <TopPosts content={content} />
                </Col>
                <Col lg={6} sm={12}>
                    <PopularProduct content={content} />
                </Col>
            </Row>
        </Container>
    }
}

export default Shopon