import React, { Component } from 'react'
import ReactApexChart from 'react-apexcharts'

export default class BreakDownChart extends Component {
    state = {
        series: [ (this.props.breakDownItem?.assisted % 1).toFixed(3) * 1 !== (this.props.breakDownItem?.assisted % 1) ? parseFloat(parseFloat(this.props.breakDownItem?.assisted).toFixed(2)) : parseFloat(this.props.breakDownItem?.assisted), parseFloat(this.props.breakDownItem?.ctaClick)],
        options: {
            chart: { type: 'donut',  },
            plotOptions:{ pie: 
                {   donut: { 
                    legend: { show: false, }, 
                    labels: { show: true, total: { showAlways: true, show: true } ,  } 
                    }
                }
            },
            dataLabels: { enabled: false },
            colors: this.props.colors,
            legend: { show: false, position: 'right' }, 
            labels: this.props.website ? this.props.cartLabel : this.props.recordVisitorCount.labels,
        },
    };

    render() {
        const {className} = this.props
        return (
            <div className={className}>
                <ReactApexChart options={this.state.options} series={this.state.series} type="donut" height={250} />
            </div>
        )
    }
}