import { Component } from "react";
import { Badge, Card, Col, OverlayTrigger, Row, Tooltip } from "react-bootstrap";
import { connect } from "react-redux";
import { kFormatter } from "../../../../../utils";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";

class ProductData extends Component{
    render(){
        const { conversion, loader } = this.props
        const color = ['blue', 'orange', 'yellow', 'green']
        return  <Row className="gx-4">
                    {conversion?.series?.map((item, index) => {
                        return <Col lg={3} sm={6} className="col-6" key={index}>
                            <Link to={`/analytics`} title="analytics">
                                <Card className={`gutter-b card-shadow ${color[index]}`}>
                                    <Card.Body>
                                        <div className="f-between">
                                            <p className="fs-8 fw-medium text-muted mb-2">
                                                {item?.name}
                                            </p>
                                            <OverlayTrigger placement='top' overlay={<Tooltip>{item.tool_tip}</Tooltip>} >
                                                <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                            </OverlayTrigger>
                                        </div>
                                        <div className="d-flex align-items-center">
                                            <p className="h1 mb-0">
                                            { index == 0 ? conversion.currency : ''} {loader ? 0 : kFormatter(item.data) }
                                            </p>                                        
                                        </div>
                                    </Card.Body>
                                </Card>
                            </Link>
                        </Col>
                    })
                    }
                </Row>
    }
}

const mapStateToProps = state => {
    return {
        conversion: state.analyticsConversion?.conversion_data, 
        loader: state.analyticsConversion?.conversion_loader, 
    }
}

export default connect(mapStateToProps) (ProductData)