import { Component } from "react";
import { Card, Nav, Tab } from "react-bootstrap";
import PostNetwork from "../Overview/component/PostNetwork";
import PostType from "../Overview/component/PostType";
class PostChart extends Component{
    render(){
        const {loader, postNetwork, loaderData } = this.props
        const heading = window?.APP_CONTENT?.Analytics?.headings ?? {};

        return (
            <Tab.Container defaultActiveKey="postType">
                <Card className='card-stretch gutter-b p-0 card-post-chart'>
                    <Card.Body className="p-0">
                        <Nav variant="underline" className="flex-row px-3 px-sm-5 border-bottom">
                            <Nav.Item>
                                <Nav.Link eventKey="postType" className={'fs-8 py-2 py-sm-3'}>{heading?.post_type ?? "Post type"}</Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link eventKey="postNetwork" className={'fs-8 py-2 py-sm-3'}>{heading?.post_network ?? "Post network"}</Nav.Link>
                            </Nav.Item>
                        </Nav>
                        <Tab.Content>
                            <PostType {...{postNetwork, loader, loaderData}} />
                            <PostNetwork {...{postNetwork, loader, loaderData}} />
                        </Tab.Content>
                    </Card.Body>
                </Card>
            </Tab.Container>
        )
    }
}

export default PostChart