import { Component } from "react";
import { connect } from "react-redux";
import { Card } from "react-bootstrap";
import AreaChart from "../../CommonComponents/AreaChart";

const loaderData = {
    "series": [
        { "name": "Total visitors", "data": [] },
        { "name": "Total post clicks", "data": [] },
        { "name": "CTA clicks", "data": [] },
        { "name": "CTR%", "data":  [] } 
    ],
    "labels": ["","",""],
}

class WebOverview extends Component{

    state={
        website: null
    }

    componentDidMount(){
        const {loader, website} = this.props
        if(!loader){
            this.setState({website: website})
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.props.loader != nextProps.loader){
            this.setState({website: nextProps.website})
        }
    }

    render(){
        const { website } = this.state
        const {loader} = this.props
        return <Card className={`gutter-b card-shadow`}>
            <Card.Header>
                <h4>Overview</h4>
            </Card.Header>
            <Card.Body>
                <AreaChart chartData={loader ? loaderData : website}/>
            </Card.Body>
        </Card>
    }
}

const mapStateToProps=(state)=>{
    return {
        website: state.analyticsWebsite?.websiteOverview_data, 
        loader: state.analyticsWebsite?.websiteOverview_loader,  
    }
}

export default connect(mapStateToProps)(WebOverview)